
.menus {
  color: #999;
  font-size: 18px;
  width: 200px;
  height: 100%;
  background: #202020;
  text-align: center;
}
.menus .user {
  height: 230px;
  width: 100%;
}
.menus .user img {
  border-radius: 50%;
  width: 150px;
  height: 150px;
  margin: 30px 20px 10px;
  -webkit-transition: all 8s;
  transition: all 8s;
}
.menus .user img:hover {
  -webkit-transform: rotate(360deg);
          transform: rotate(360deg);
}
.menus li {
  height: 60px;
  margin: 5px;
}
.menus li a {
  height: 100%;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.menus li a:hover {
  color: #fff;
}


